import DrawerInitiator from '../utils/drawer-initiator.js';
import UrlParser from '../routes/url-parser.js';
import routes from '../routes/routes.js';

class App {
  constructor({ button, drawer, content }) {
    this._button = button;
    this._drawer = drawer;
    this._content = content;

    this._initAppShell();
  }

  _initAppShell() {
    DrawerInitiator.init({
      button: this._button,
      drawer: this._drawer,
      content: this._content,
    });
  }

  async renderPage() {
    const url = UrlParser.parseActiveUrlWithCombiner();
    const page = routes[url];

    console.log('üîç Current URL:', window.location.hash);
    console.log('üîç Parsed URL:', url);
    console.log('üîç Found page:', !!page);

    if (!page) {
      console.error('‚ùå Page not found for URL:', url);
      this._content.innerHTML = '<h1>Halaman tidak ditemukan üò¢</h1>';
      return;
    }

    console.log('‚úÖ Rendering page for:', url);

    // üé¨ VIEW TRANSITION API
    if ('startViewTransition' in document) {
      console.log('üé¨ Using View Transition API');
      
      document.startViewTransition(async () => {
        this._content.innerHTML = await page.render();
        await page.afterRender?.();
        console.log('‚úÖ Page rendered successfully');
      });
    } else {
      console.warn('‚ö†Ô∏è View Transition API not supported');
      this._content.innerHTML = await page.render();
      await page.afterRender?.();
      console.log('‚úÖ Page rendered successfully (fallback)');
    }
  }
}

export default App;